html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
  color: $text;
  background-color: $white;
  width: 100%;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  padding: 0;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  outline: transparent;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-btn {
  display: inline-block;
  padding: 0px 10px;
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 12px;
  line-height: 1.5;
  color: $white;
  text-transform: uppercase;
  text-align: center;
  border-radius: 25px;
  background-color: $accent;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  outline: transparent;

  @include desktop {
    &:hover {
      background-color: $bg;
    }
  }

  &:focus {
    box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.75);
  }

  &:active {
    transform: translateY(3px);
  }
}

.link-btn {
  width: 65px;
  height: 65px;
  background-color: $accent;
  border-radius: 50%;
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 1, 0.24);
  position: absolute;
  outline: transparent;

  @include desktop {
    &:hover {
      background-color: $bg;
    }
  }

  &:focus {
    box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.75);
  }

  &:active {
    transform: translateY(3px);
  }
}

.title {
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 30px;
  color: $black;
  text-transform: uppercase;
}

.subtitle {
  display: block;
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 14px;
  color: $silver;
  text-transform: uppercase;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1130px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
